<script>
import AxiosHelper from '../components/AxiosHelper.vue';
import Notifications from '../components/Notifications.vue';

const dateFormat = new Intl.DateTimeFormat('en', {year: 'numeric', month: 'short', day:'numeric'});
  const repoShowIncrement = 6;

  export default {
    name: 'Projects',

    components: {Notifications},
    mixins: [AxiosHelper],

    data: () => ({
      itchPageHref: 'https://teonistor.itch.io/',
      anthologyGames: [
        {year: 2021, name: 'Needle in a Haystack',   href: 'needle-in-a-haystack'},
        {year: 2020, name: 'Re:Pair',                href: 're-pair'},
        {year: 2019, name: 'Attac, Protec, Prospec', href: 'attac-protec-prospec'},
        {year: 2018, name: 'Relay',                  href: 'relay'},
        {year: 2017, name: 'Cold War, even colder',  href: 'cold-war-even-colder'},
        {year: 2016, name: 'Soul Stroll',            href: 'soul-stroll'}
      ],
      compGames: [
        {name: 'Re:Bounce',          href: 're-bounce'},
        {name: 'The Stanley Parody', href: 'stanley-parody'},
        {name: 'Darkness Simulator', href: 'darksim'}
      ],
      otherGames: [
        {name: 'Snake N',      href: 'snake-n'},
        {name: 'Sky Showdown', href: 'sky-showdown'}
      ],
      repoShowCount: 5,
      recentRepos: []
    }),

    computed: {
      truncatedRepos () {
        return this.recentRepos.filter((repo, index) => index < this.repoShowCount);
      }
    },

    methods: {
      fixDescription(str) {
        if (!str)
          return '';
        if (str.endsWith('. '))
          return str;
        if (str.endsWith('.'))
          return str + ' ';
        return str + '. ';
      },

      fixPost(language, updated) {
        language = language || '';

        try {
          updated = 'Updated ' + dateFormat.format(new Date(updated));
        } catch (e) {
          console.error(e);
          updated = '';
        }

        if (!language || !updated)
          return language + updated;
        return language + '. ' + updated;
      },

      showAdditionalRepos() {
        this.repoShowCount += repoShowIncrement;
      },

      setRecentRepos(data) {
        data = data.sort((l,r) => l.updated_at > r.updated_at ? -1 : 1);
        this.recentRepos = data.map(big => ({
          name: big.name,
          href: big.html_url,
          description: this.fixDescription(big.description),
          post: this.fixPost(big.language, big.updated_at)
        }));
      }
    },

    mounted() {
      this.plainAxios('calling Github API', axios => axios.get(this.$githubApiUrl + '/users/teonistor/repos'),
        data => this.setRecentRepos(data));
    }
  }
</script>

